import React, {useEffect, useState} from 'react'
import './utils/MakePage.css'
import { dbService } from '../tools/fbase';

const MakePage = () => {
    const [s1title, setS1title] = useState("");
    const [s2title, setS2title] = useState("");
    const [s3title, setS3title] = useState("");
    const [attachment, setAttachment] = useState("")
    const [attachment1, setAttachment1] = useState("")
    const [attachment2, setAttachment2] = useState("")
    const [mainColor, setMainColor] = useState("yellow");
    const [sectionOneImageLeft, setSectionOneImageLeft] = useState(0);
    const [secNum, setSecNum] = useState(1);

    const onSubmit = async () => {

        // 사진을 먼저 업로드하고 그 URL을 받아서 데이터로 넣어줘야한다.
        // const attachmentRef = stService.ref().child(`${userObj.uid}/${uuidv4()}`)
        // const response = await attachmentRef.putString(attachment, "data_url");
        // const attachmentURL = await response.ref.getDownloadURL();

    }

    const onFileChange =  (e, num) => {
        console.log("e", num);
        const {target:{files},} = e;
        const oneFile = files[0];
        const reader = new FileReader();
        reader.onloadend = (finishedEvent) => { // 로딩이 끝날 때 실행한다는 뜻.
            const {currentTarget:{result}} = finishedEvent;
            switch (num){
                case 1:
                    setAttachment(result);
                    break;
                case 2:
                    setAttachment2(result);
                    break;
                case 3:
                    setAttachment(result);
                    break;

            }
        }
        reader.readAsDataURL(oneFile);
    }

    const selectorTable = () => {
        switch (secNum){
            case 1:
                return <>
                    <div>
                        1번
                        <input value={s1title} type="text" onChange={e => setS1title(e.currentTarget.value)}/>
                        <input type="file" accept="image/*" className="file-input" onChange={(e) => onFileChange(e, 1)}/>
                        왼쪽
                        <input value={sectionOneImageLeft} type="number" onChange={e => setSectionOneImageLeft(e.currentTarget.value)}/>
                    </div>
                </>
            case 2:
                return <>
                <div>
                    2번
                    <input value={s2title} type="text" onChange={e => setS2title(e.currentTarget.value)}/>
                    <input type="file" accept="image/*" className="file-input" onChange={e => onFileChange(e,2)}/>
                    왼쪽
                </div>
                </>
            case 3:
                return <>
                <div>
                    3번
                    <input value={s3title} type="text" onChange={e => setS3title(e.currentTarget.value)}/>
                    <input type="file" accept="image/*" className="file-input" onChange={onFileChange}/>
                    왼쪽
                </div>
                </>
        }
    }

    return (
        <div className="make-page-container">
            <div className="make-page-example">
                <div className="main-page-section" style={{backgroundColor:`${mainColor}`}}>
                    Top
                </div>
                <div className="main-page-section">
                    <div className="main-section-left">
                        {s1title}
                    </div>
                    <div className="main-section-right">
                        {attachment && 
                        <div>
                            <img src={attachment} className="section-image" style={{marginTop:`${sectionOneImageLeft}px`}}/>
                        </div>
                        }
                    </div>
                </div>
                <div className="main-page-section">
                    <div className="main-section-left">
                        {attachment2 && 
                        <div>
                            <img src={attachment2} className="section-image" style={{marginTop:`${sectionOneImageLeft}px`}}/>
                        </div>
                        }
                    </div>
                    <div className="main-section-right">
                        {s2title}
                    </div>
                </div>
                <div className="main-page-section">
                    신청받는 곳
                </div>
                예시
            </div>
            <div className="make-page-make-space">
                <div className="section-selector-container">
                    <span className="make-page-section-selector" onClick={(e) => setSecNum(1)}>
                        1번
                    </span>
                    <span className="make-page-section-selector" onClick={(e) => setSecNum(2)}>
                        2번
                    </span>
                    <span className="make-page-section-selector" onClick={(e) => setSecNum(3)}>
                        3번
                    </span>
                </div>
                {selectorTable()}
                <input value={mainColor} type="color" onChange={e => setMainColor(e.currentTarget.value)}/>
                <button onClick={onSubmit}>등록</button>
                작성
            </div>
        </div>
    )
}

export default MakePage
